@import url(https://fonts.googleapis.com/css?family=Encode+Sans|Lato&display=swap);
*{
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}
nav {
  width: 100%;
  height: 10%;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: rgba(25, 26, 31, 0.9);
  border-bottom: 0.5px solid #8743cc;
  z-index: 2;
}
nav .logo {
  margin-left: 5vw;
  width: 35px;
}
nav div img {
  width: 35px;
  opacity: 0.9;
  margin-right: 5vw;
}
.container-supreme {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0;
}
.container-main {
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.container-main h1 {
  text-align: center;
  font-size: 4rem;
  color: #f1f1f1;
  font-family: 'Encode Sans', sans-serif;
  letter-spacing: 5px;
  z-index: 2;
}
.container-main h2 {
  text-align: center;
  font-size: 1.5rem;
  color: #f1f1f1;
  font-family: 'Encode Sans', sans-serif;
  letter-spacing: 5px;
}
.container-main hr {
  margin: 1em 5em;
  border: 2px solid #8743cc;
}
.container-main img {
  width: 20rem;
}
.container-about{
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.container-about h2 {
  margin-top: 10vh;
  font-size: 2rem;
  color: #f1f1f1;
  font-family: 'Encode Sans', sans-serif;
}
.block-color-about {
  border-radius: 5px;
  background-color: rgba(51, 62, 74, 0.7);
  margin: 25px;
  display: flex;
  align-items: flex-start;
  padding: 25px;
}
.block-color-about img {
  height: 41vh;
  margin-right: 25px;
  border-radius: 5px;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.block-color-about p {
  font-size: 1.5rem;
  color: #f1f1f1;
  font-family: 'Lato', sans-serif;
}

@media (max-width: 600px) {
  .container-main h1{
    font-size: 3rem;
  }
  .container-main h2{
    font-size: 1rem;
  }
  .container-main img{
    width: 15rem;
  }
}

@media (max-width: 880px) {
  .block-color-about {
    flex-direction: column;
    align-items: center;
  }
  .block-color-about p{
    text-align: center;
    margin-top: 25px;
  }
}

@media (max-width: 1000px) {
  .container-main {
    height: 120vh;
    flex-direction: column;
  }
  .container-about {
    height: 120vh;
  }
}
.container-particles {
  height: 100vh;
  background-color: #191A1F;
}

@media (max-width: 1000px) {
  .container-particles {
    height: 120vh;
  }
}
